import { Figther } from "./fighter"

/**
 * Clase que almacena información sobre los personajes de todos los universos considerados
 */
export class Pokedex {
  constructor(private luchadores: Figther[]) {
  }

  /**
   * Método que añade un luchador nuevo a la coleccion de luchadores
   * @param peleador Luchador que se va a añadir
   */
  addFighter(peleador: Figther) {
    this.luchadores.push(peleador);
  }

  /**
   * Metodo que permite eliminar un luchador de un array
   * @param item Luchador del array a borrar
   */
   removeItem(peleador: Figther) {
    let pos: number = this.luchadores.indexOf(peleador);
    this.luchadores.splice(pos, 1);
  }
}